import {Link} from "gatsby"
import PropTypes from "prop-types"
import React from "react"
import "./header.css"
import "./mobileHeader.css"
import foto1 from "../../images/download.svg"

function addIcon(submenus) {
    if (submenus != null) {
        return (
            <i style={{marginLeft: '4px'}} className="fa fa-caret-down"></i>
        )
    }
}


function submenus(submenus) {
    if (submenus != null) {
        return (submenus.map((item, key) => {
            return (
                <a href={item.url} style={{textDecoration: 'none !important'}}>
                <div class="secondarylistClass list">
                    <a style={{
                        color: `white`,
                        padding: `0px`,
                        textDecoration: 'none !important'
                    }}> {item.text}</a>
                </div>
                </a>
            )
        }))
    }
}

function submenusMobile(submenus) {
    if (submenus != null) {
        return (submenus.map((item, key) => {
            return (
                <div style={{marginTop:'5px',textDecoration: 'none'}}>
                    <a href={item.url} style={{
                        color: `white`,
                        padding: `0px`,
                        textDecoration: 'none'
                    }}> {item.text}</a>
                </div>
            )
        }))
    }
}
var time;
function clickMobileMenu() {
    if(document.getElementById("mobilePanel").getAttribute("style").includes("none"))
    {
        time=0;
        window.addEventListener("click",EventListenner);
        document.getElementById("mobilePanel").setAttribute("style", "display:block;")
    }
    else{
        document.getElementById("mobilePanel").setAttribute("style", "display:none;")
        window.removeEventListener("click",EventListenner);
    }
}
function EventListenner() {
    if (document.getElementById("mobilePanel").getAttribute("style").includes("block") && time != 0) {
        document.getElementById("mobilePanel").setAttribute("style", "display:none;")
        window.removeEventListener("click",EventListenner);
    }
    time++;
}

const Header = ({linksData, selectedItem}) => (
    <header>
        <link rel="stylesheet"
              href="https://cdnjs.cloudflare.com/ajax/libs/font-awesome/4.7.0/css/font-awesome.min.css"></link>
        <link rel="stylesheet" type="text/css" href="src/fonts/fonts.min.css" />
        {/* googleAnalytics Scripts */}
        <div class="listHeaderContainer">
            <div className="mobileHeader">
                <div href="#" id="mobileIcon" onClick={() => clickMobileMenu()}><i
                    className="fa fa-bars mobileIcon"></i></div>
                <div id="mobilePanel"  style={{display:'none'}} className="mobilePanel">
                    {linksData.map((item, key) => {
                        return (
                            <div style={{marginTop:'5px'}}>
                                <Link
                                    to={item.url} listClass
                                    style={{color: `white`, textDecoration: 'none'}}
                                >
                                    {item.text}
                                </Link>
                                {/*<div style={{paddingLeft: '25px'}}>*/}
                                {/*    {submenusMobile(item.submenus)}*/}
                                {/*</div>*/}
                            </div>
                        )
                    })}
                </div>
            </div>
            <img src={foto1} class="logo" alt="Logo"></img>
            {linksData.map((item, key) => {
                return (
                    <div className="listContainer" style={{zIndex: '1000'}}>
                        <div class="desktopHeader">
                            <a href={item.url} style={{textDecoration: 'none'}}>
                                <div class="listClass list">
                                    <a
                                        style={{color: `white`, textDecoration: 'none'}}
                                    >
                                        {item.text}{addIcon(item.submenus)}
                                    </a>
                                </div>
                            </a>
                            {/*<div>*/}
                            {/*    {submenus(item.submenus)}*/}
                            {/*</div>*/}
                        </div>
                    </div>
                )
            })}
        </div>
    </header>
)

Header.propTypes = {
    siteTitle: PropTypes.string,
}

Header.defaultProps = {
    siteTitle: ``,
}

export default Header
