import { Link } from "gatsby"
import PropTypes from "prop-types"
import React from "react"
import Image from "../image"
import "./headerIndex.css"



const IndexHeader = ({ siteTitle,linksData,disableNavBar}) => {
  function navBar(){
    if(linksData !== undefined){
    linksData.map((item, key) => {
      return(
        <li >
        <h1  class="indexMenu">
          <Link to={item.url} class="headerIndexLink">
            {item.text}
          </Link>
          </h1>
        </li>
      )
    })
  }
  }
  return(
    <>
  <header
    style={{
      position: `relative`
    }}
  >
   {/* googleAnalytics Scripts */}
  <script async src="https://www.googletagmanager.com/gtag/js?id=UA-164290593-1" />
  <script>
    {`
       window.dataLayer = window.dataLayer || [];
       function gtag(){dataLayer.push(arguments);}
       gtag('js', new Date());
       gtag('config', 'UA-164290593-1');
    `}
  </script>
  
    <div class="headerIndexNavBar" style={disableNavBar ? { visibility: "hidden" } : { visibility: "visible" }}>
     <ul>
     {navBar()}
</ul> 
    </div>
    <div class="firstTitleContainer">
        <h1 class="firstTitle">
      Tipos de vitmainas</h1>
    </div >
    <div class="secondTitleContainer">
        <h2 class="secondTitle">Que son las vitaminas y que alimentos las contienen</h2>
    </div >
    <Image/>
  </header>
  </>
)
  }


IndexHeader.propTypes = {
  siteTitle: PropTypes.string,
}

IndexHeader.defaultProps = {
  siteTitle: ``,
}

export default IndexHeader
