
import React from "react"

const ProductFooter = () => (

<footer style={{
        bottom: `0`,
        position: `absolute`,
        width:`100%`,

          }}>

            <div style={{
            paddingBottom:`50px`
          }}>
            
            <div style={{
            textAlign:`left`,
            float:`left`,
            fontSize:`2vh`
          }}>
            
            
            Copyright  {new Date().getFullYear()} vitaminasalimentos
            </div>

            <div id="footerIndex">
            Powered by vitaminasalimentos
            </div>
            </div>
        </footer>
        )


        export default ProductFooter