
import React from "react"

const ProductSideFooter = () => (

    <footer style={{
        bottom: `0`,
        position: `absolute`,
        width:`80%`,
        left: '10%'
    }}>

        <div style={{
            paddingBottom:`50px`
        }}>

            <div style={{
                textAlign:`center`,
                float:`left`,
                fontSize:`2vh`
            }}>


                Copyright  {new Date().getFullYear()} vitaminasalimentos
            </div>

            <div id="footerIndex">
                Powered by vitaminasalimentos
            </div>
        </div>
    </footer>
)


export default ProductSideFooter